.inner {
  background-color: black;
  color: white;
  width: 380px;
  height: 680px;
  margin: auto;
  margin-top: 100px;
  font-size: 18px;
  border-radius: 30px;
  position: relative;
}

.header {
    padding: 20px;
    letter-spacing: 2px;
    font-weight: 800;
    font-size: 24px;
    text-align: center;
}

.input {
    width: 300px;
    height: 36px;
    border-radius: 20px;
    text-align: center;
    margin: auto;
    margin-bottom: 20px;
    background-color: white;
    color: black;
}

.myname {
    font-weight: 600;
    letter-spacing: 1px;
}

.my-info {
    border-bottom: 1px solid white;
    padding-bottom: 20px;
    text-align: center;
    color: hotpink;
}
.mypic {
    font-size: 24px;
    margin: 10px;
}

.container {
    margin-top: 20px;
    margin-left: 40px;
    display: flex;
    justify-content: space-between;
    margin-right: 20px;
}

.firstName, .lastName {
    font-size: 18px;
    font-weight: 600;
}

.lastName {
    font-style: italic;
    text-transform: uppercase;
}

.gender {
    font-size: 24px;
    color: yellow;
}

.number {
    font-size: 14px;
    text-align: right;
}

.call {
    font-size: 36px;
    color: green;
}

.othericons {
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
    color: khaki;
    font-size: 24px;
    position: absolute;
    bottom: 0px;

}

.star, .clock, .person, .other {
    margin-right: 40px;
    margin-left: 10px;
    margin-bottom: 16px;
}

.auto {
    margin-left: 10px;
    margin-bottom: 16px;
}